import { ajaxPro } from "req/http/http";

export function addIP(param) {
  return ajaxPro({
    url: "/allowip/add",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function editIP(param) {
  return ajaxPro({
    url: "/allowip/modify",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}

export function delIP(param) {
  return ajaxPro({
    url: "/allowip/del",
    data: param,
    type: "post"
  })
    .then(res => {
      return Promise.resolve(res);
    })
    .catch(err => {
      return Promise.reject(err);
    });
}



