<template>
  <div class="system-managehost-add">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="80"
      ref="system_managehost_add"
    >
      <mu-form-item prop="ip" label="IP地址" :rules="ipRules">
        <mu-text-field v-model="formData.ip" :max-length="50"></mu-text-field>
      </mu-form-item>
      <mu-form-item>
        <mu-button color="primary" small @click="submitAdd" v-if="isAdd">添加</mu-button>
        <mu-button color="primary" small @click="submitEdit" v-if="!isAdd">修改</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { addIP, editIP } from "req/api/system/managehost.js";

export default {
  name: "system_managehost_add",
  data() {
    return {
      labelPosition: "right",
      ipRules: [
        { validate: val => !!val, message: "IP地址不能为空" },
        {
          validate: val => this.$validates.ipReg.test(val),
          message: "请输入正确的IP地址,例如:1.1.1.1"
        }
      ],
      formData: {
        id: "",
        ip: ""
      }
    };
  },
  props: {
    layer_data: {
      type: [Object, Array, Number],
      default: () => {
        return {};
      }
    },
    isAdd: {
      type: Boolean,
      default: true
    },
    layerid: {
      type: String
    }
  },
  created() {
    if (!this.isAdd) {
      this.formData.id = this.layer_data.id;
      this.formData.ip = this.layer_data.ip;
    }
  },
  methods: {
    submitAdd() {
      this.$refs.system_managehost_add.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_managehost",
            "确定添加吗？",
            () => {
              this.__addIP(this.formData);
            },
            this
          );
        }
      });
    },
    submitEdit() {
      this.$refs.system_managehost_add.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_managehost",
            "确定修改吗?",
            () => {
              this.__editIP(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_managehost",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __addIP(param) {
      addIP(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "添加成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.system_managehost_table.reload();
        })
        .catch(err => {});
    },
    __editIP(param) {
      editIP(param)
        .then(res => {
          this.$message({
            type: "success",
            message: "修改成功"
          });
          this.$layer.close(this.layerid);
          this.$parent.$refs.system_managehost_table.reload();
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-managehost-add {
  width: 100%;
  padding: 10px 20px 0 0;
}
</style>





