<template>
  <div class="system-managehost">
    <g-el-table
      :tableHeadShow="tableHeadShow"
      :url="url"
      :type="type"
      :cloumns="cloumns"
      :params="params"
      :pageParams="pageParams"
      @selectChange="selectChange"
      ref="system_managehost_table"
    >
      <template v-slot:table_header>
        <el-button type="primary" size="mini" @click="add">增加IP</el-button>
        <el-button type="primary" size="mini" @click="configSet">功能设置</el-button>
        <el-button type="danger" size="mini" @click="del(selectData)">批量删除</el-button>
      </template>
      <!-- 操作 -->
      <template v-slot:operation="props">
        <el-button type="primary" size="mini" @click="edit(props.data.row)">编辑</el-button>
      </template>
      <!-- IP地址 -->
      <template v-slot:ip_head="props">
        <el-input v-model="params.ip" :maxLength="20" size="mini" placeholder="请输入内容" @keyup.enter.native="search"></el-input>
      </template>
      <!-- 上线时间 -->
      <template v-slot:login_time_head="props">
        <el-date-picker
          type="date"
          size="mini"
          placeholder="选择日期"
          v-model="params.login_time"
          @change="search"
          style="width: 100%;"
        ></el-date-picker>
      </template>
    </g-el-table>
  </div>
</template>
<script>
import add from "views/system/managehost/add";
import configSet from "views/system/managehost/configSet";
import {delIP} from "req/api/system/managehost"
export default {
  data() {
    return {
      url: "/allowip/list",
      type: "get",
      cloumns: [
        {
          type: "selection",
          fixed: "left"
        },
        {
          type: "index",
          // fixed: "left",
          label: "序号",
          second_head: {
            type: "index",
            width: "60px",
            label: " ",
            indexMethod: function(index) {
              return index + 1;
            }
          }
        },
        {
          label: "IP地址",
          sortable: true,
          prop: "ip",
          sortMethod: this.$tools.IPSort,
          second_head: {
            prop: "ip",
            width: "200px",
            templateHeadName: "ip_head"
          }
        },
        {
          label: "上次登录时间",
          prop: "login_time",
          sortable: true,
          second_head: {
            width: "200px",
            templateHeadName: "login_time_head",
            prop: "login_time"
          }
        },
        {
          label: "操作",
          second_head: {
            label: "",
            minWidth: "100px",
            isTemplate: true,
            templateName: "operation"
          }
        }
      ],
      tableHeadShow: true,
      params: {
        page: 1,
        limit: 10,
        ip: "",
        login_time: ""
      },
      pageParams: {
        layout: "total, sizes, prev, pager, next, jumper",
        pageAlign: "left",
        pageSizes: [10, 20, 30]
      },
      selectData: []
    };
  },
  methods: {
    reload() {
      this.$refs.system_managehost_table.reload();
    },
    search() {
      this.params.page = 1
      this.reload()
    },
    selectChange(select) {
     this.selectData = select
    },
    configSet() {
      this.$layer.iframe({
        content: {
          content: configSet,
          parent: this,
          data: {}
        },
        area: ["600px", "auto"],
        title: "功能设置",
        shadeClose: false
      });
    },
    add() {
      this.$layer.iframe({
        content: {
          content: add,
          parent: this,
          data: {}
        },
        area: ["350px", "auto"],
        title: "增加IP地址",
        shadeClose: false
      });
    },
    edit(item) {
      this.$layer.iframe({
        content: {
          content: add,
          parent: this,
          data: {
            layer_data: item,
            isAdd: false
          }
        },
        area: ["350px", "auto"],
        title: "修改IP地址",
        shadeClose: false
      });
    },
    del(item) {
      let ids = ""
      if(Array.isArray(item)) {
        if(this.$tools.isArrayNull(item)) {
          this.$message({
            type:"error",
            message:"请选择需要删除的选项!"
          })
          return
        }
        ids = this.$tools.mergeId(item)
      }else {
        ids = item.id
      }
      this.$isShowTip(
        "system",
        "system_managehost",
        "确定删除吗?",
        () => {
          this.__delIP({ id: ids });
        },
        this
      );
    },
    __delIP(param) {
      delIP(param).then(res => {
        this.$message({
          type:"success",
          message:"删除成功"
        })
        this.reload()
      }).catch(err => {})
    }
  }
};
</script>


