<template>
  <div class="system-managehost-configset">
    <mu-form
      :model="formData"
      class="mu-demo-form"
      :label-position="labelPosition"
      label-width="100"
      ref="system_managehost_configset"
    >
      <el-divider content-position="left">白名单模式</el-divider>
      <mu-form-item prop="status" label="工作模式">
        <mu-radio
          v-model="formData.status"
          v-for="item of dropDownData.status"
          :key="item.id"
          :value="item.id"
          :label="item.name"
        ></mu-radio>
      </mu-form-item>
      <transition name="fade">
        <div v-show="formData.status == 1">
          <el-divider content-position="left">时间范围</el-divider>
          <mu-form-item prop="time_range" label="功能生效期">
            <mu-radio
              v-model="formData.time_range"
              v-for="item of dropDownData.time_range"
              :key="item.id"
              :value="item.id"
              :label="item.name"
            ></mu-radio>
          </mu-form-item>
        </div>
      </transition>
      <mu-form-item>
        <mu-button color="primary" small @click="submitEdit">提交</mu-button>
        <mu-button @click="cancel" small>取消</mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { setConfig, getConfig } from "req/api/serverconfig/configset.js";

export default {
  name: "system_managehost_configset",
  data() {
    return {
      labelPosition: "right",
      formData: {
        status: "1",
        time_range: "0",
        type:"ipWhiteSetting"
      },
      dropDownData: {
        status: [
          {
            id: "1",
            name: "自动添加"
          },
          {
            id: "0",
            name: "人工添加"
          }
        ],
        time_range: [
          {
            id: "0",
            name: "永久"
          },
          {
            id: "1",
            name: "30分钟"
          },
          {
            id: "2",
            name: "1小时"
          },
          {
            id: "3",
            name: "8小时"
          },
          {
            id: "4",
            name: "16小时"
          }
        ]
      }
    };
  },
  props: {
    layer_data: {
      type: [Object, Array, Number],
      default: () => {
        return {};
      }
    },
    isconfigset: {
      type: Boolean,
      default: true
    },
    layerid: {
      type: String
    }
  },
  created() {
    this.__getConfig({ type: "ipWhiteSetting" });
  },
  methods: {
    submitEdit() {
      this.$refs.system_managehost_configset.validate().then(result => {
        if (result) {
          this.$isShowTip(
            "system",
            "system_managehost",
            "确定提交吗?",
            () => {
              this.__setConfig(this.formData);
            },
            this
          );
        }
      });
    },
    cancel() {
      this.$isShowTip(
        "system",
        "system_managehost",
        "确定取消吗？",
        () => {
          this.$layer.close(this.layerid);
        },
        this
      );
    },
    __getConfig(param) {
      getConfig(param)
        .then(res => {
          this.formData.status = res.data.status || "1";
          this.formData.time_range = res.data.time_range || "0";
        })
        .catch(err => {});
    },
    __setConfig(param) {
      setConfig(param)
        .then(res => {
          this.$message({
            type:"success",
            message:"设置成功"
          })
          this.$layer.close(this.layerid);
        })
        .catch(err => {});
    }
  }
};
</script>
<style scoped>
.system-managehost-configset {
  width: 100%;
  padding: 10px 20px 10px 10px;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.fade-enter-active,
.fade-leave {
  transition: all 0.5s;
}
</style>





